<template>
  <h2 hidden>{{state.uid = $route.params.id}}</h2>
  <div>
    <table class="table table-hover" style="table-layout: fixed">
      <thead>
        <tr>
          <th>목표 이름</th>
          <th>목표 내용</th>
          <th>성취 날짜</th>
          <!-- <th>Success Quantity</th> -->
          <th>이룬 양</th>
          <th>성취률</th>
        </tr>
      </thead>
      <tbody v-for="(list, i) in state.lists" :key="(list, i)">
        <tr v-if="list.goalPublic">
          <td class="content">
            <RouterLink :to="{name: 'goalreadForPublic', params:{id:list.goalid}}"
              style="text-decoration:none;color: rgb(6,90,92);font-weight: 500;">{{ list.name }}</RouterLink>
          </td>
          <td class="content">{{ list.content }}</td>
          <td>{{ list.dueDate }}</td>
          <!-- <td>{{ list.successQ }}{{list.unit}}</td> -->
          <td>{{ list.qdone }}{{list.unit}}</td>
          <td>{{ list.perCentDone }}%</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>
<script>
import { onMounted } from 'vue'
import { reactive, ref } from '@vue/reactivity'
import axios from 'axios';
export default {
  setup() {
    onMounted(() => { loginHandler() })
    const state = reactive({
      lists: '',
      uid: '',
      email: sessionStorage.getItem('email'),
      token: sessionStorage.getItem('TOKEN'),
    });
    const loginHandler = async () => {

      const url = "http://localhost:9090/api/goalListForPublic"
      const headers = {
        "Content-Type": "application/json",
        "Authorization": state.token,
        "token": state.token
      }
      const body = { uid: state.uid }
      await axios.post(url, body, { headers }).then(function (res) {
        if (res.data != null) {
          state.lists = res.data
        } else {
          alert('too bad. check something')
        }
      })
    }
    return { state, loginHandler }
  }
}
</script>
<style scoped>
.content {
  width: 10px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  word-break: break-all;
}
</style>