<template>
  <h2 hidden>{{state.uid=$route.params.id}}</h2>
  <div class="container text-center">
    <div class="row">
      <div class="col-lg-6 col-sm-12">
        <!-- left -->
        <div class="row">
          <div class="col-lg-3 col-sm-3 imgDiv">
            <!-- profile picture -->
            <img v-if="!(state.urltest==null)" v-bind:src="state.img" class="img-thumbnail rounded-circle "
              alt="대표이미지" />
            <img v-else="state.urltest==null" src="../assets/images/pictures/profile_icon.png"
              class="img-thumbnail rounded-circle  " alt="대표이미지">
          </div>
          <div class="col-lg-9 col-sm-9">
            <!-- profile info -->
            <h3>나의 페이지</h3>

            <div class="col" v-if="state.emailPublic==true">
              <div class="input-group mb-3  ">
                <span class="input-group-text labelLength6" id="basic-addon1">이메일</span>
                <input type="text" class="form-control" v-model="state.email" disabled>
              </div>
            </div>
            <div class="col" v-else>
              <div class="input-group mb-3  ">
                <span class="input-group-text labelLength6" id="basic-addon1">이메일</span>
                <input type="text" class="form-control" v-model="state.emailNonPublic" disabled>
              </div>
            </div>

            <div class="col">
              <div class="input-group mb-3 ">
                <span class="input-group-text labelLength6" id="basic-addon1">닉네임</span>
                <input type="text" class="form-control" v-model="state.nickname" ref="nicknamee" disabled>
              </div>
            </div>

            <div class="col" v-if="state.studytopicPublic==true">
              <div class="input-group ">
                <span class="input-group-text labelLength6">나의 관심사</span>
                <textarea class="form-control" aria-label="With textarea" v-model="state.aboutme" disabled></textarea>
              </div>
            </div>
            <div class="col" v-else>
              <div class="input-group ">
                <span class="input-group-text labelLength6">나의 관심사</span>
                <textarea class="form-control" aria-label="With textarea" v-model="state.studytopicNonPublic"
                  disabled></textarea>
              </div>
            </div>
          </div>
        </div><!-- avatar & user info -->
      </div>
      <div class="col-lg-6 col-sm-12">
        <UserHistoryPublic :uid=state.uid />
      </div>
    </div>
  </div>
</template>
<script>
import { onMounted } from 'vue'
import { reactive, ref } from '@vue/reactivity'
import { useRouter } from 'vue-router';
import UserHistoryPublic from './UserHistoryPublic.vue';
import axios from 'axios'
export default {
  setup() {
    onMounted(() => { handleGetUser(); });
    const router = useRouter();
    let swaboutme = ref(null);
    let swemail = ref(null);
    const nicknamee = ref(null);
    let nickname = ref(true);
    const state = reactive({
      nickname: "",
      aboutme: "",
      studytopicPublic: "",
      emailNonPublic: "비공개",
      studytopicNonPublic: "비공개",
      emailPublic: "",
      uid: "",
      email: "",
      img: "",
      urltest: "",
      token: sessionStorage.getItem("TOKEN"),
    });
    const handleGetUser = async () => {
      const url = "http://localhost:9090/api/publicProfile";
      const headers = {
        "Content-Type": "application/json",
        Authorization: state.token,
        "token": state.token
      };
      const body = { uid: state.uid };
      await axios.post(url, body, { headers }).then(function (res) {
        if (res.status === 200) {
          state.nickname = res.data.nickname;
          state.aboutme = res.data.aboutme;
          state.email = res.data.email;
          state.urltest = res.data.path;
          state.studytopicPublic = res.data.studytopicPublic;
          state.emailPublic = res.data.emailPublic;
          swaboutme.value = res.data.studytopicPublic;
          swemail.value = res.data.emailPublic;
          showImage(res.data);
        }
      });
    };
    const deleteAccount = async () => {
      const url = "http://localhost:9090/api/deleteAccount";
      const headers = {
        "Content-Type": "application/json",
        Authorization: state.token,
        "token": state.token
      };
      const body = { email: state.email, };
      await axios.post(url, body, { headers }).then(function (res) {
        if (res.status === 200) {
          alert(state.email + " is deleted");
          router.push({ name: "home" });
        }
      });
    };
    const showImage = async (dto) => {
      const url = `http://localhost:9090/display`;
      let str = "";
      str = `${url}?fileName=${dto.thumbnailURL}`;
      state.img = str;
    };
    return { swaboutme, swemail, nickname, state, nicknamee, handleGetUser, deleteAccount, showImage };
  },
  components: { UserHistoryPublic }
}
</script>
  
<style scoped>
.labelLength6 {
  width: 7em;
}

.imgDiv {
  margin: auto;
  text-align: center;
  justify-content: center;
}

.btnhome {
  display: block;
  text-align: right;
  margin: 20px;
}
</style>
