<template>
  <h2 hidden>{{state.uid=$route.params.id}}</h2>
  <div class="listcomment">
    <table class="table table-hover">
      <thead>
        <tr>
          <th scope="col">답글 내용</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(list, i) in state.lists" :key="(list, i)">
          <td>
            <RouterLink :to="{name: 'answerread', params:{id:list.answerid}}"
              style="color:rgba(6, 90, 92, 0.9); text-decoration:none; font-weight:bold;">{{ list.content }}
            </RouterLink>
          </td>
        </tr>
      </tbody>
    </table>

  </div>

</template>


<script>
import { onMounted, reactive, ref } from 'vue'
import axios from 'axios'
import { useRouter } from 'vue-router';

export default {
  name: "TheQuestion",
  setup() {
    const value = ref(false)
    const router = useRouter()
    onMounted(() => { loginHandler() })
    const state = reactive({
      questionid: '',
      lists: '',
      uid: '',
      email: sessionStorage.getItem('email'),
      token: sessionStorage.getItem('TOKEN'),
    });
    const loginHandler = async () => {
      const url = "http://localhost:9090/api/commentlistForPublic"
      const headers = {
        //TODO send the right amound of tokent and receive the same...
        'Content-Type': 'application/json',
        Authorization: state.token,
        token: state.token,
        'name': 'test list'
      }
      const body = { uid: state.uid }
      await axios.post(url, body, { headers }).then(function (res) {
        if (res.status == 200) {
          state.lists = res.data.dto
        } else {
          alert('bad.')
        }
      })
    }
    return { value, state, loginHandler }
  }
};
</script>
<style scoped>
.answer_collapse {
  border: solid 1px grey;
  padding: 0 10px;
  margin: 10px 0;
}

.answer {
  display: flex;
}

.counter {
  display: flex;
  align-items: center;
  height: 80px;
  margin-right: 30px;
  background-color: lightgray;
  padding: 0 10px;
  border-radius: 30%;
}

.num {
  font-size: 30px;
  color: rgb(6, 90, 92);
  font-weight: bold;
  margin: 10px;
}

button {
  font-size: 20px;
  font-weight: bold;
}
</style>