<template>
  <h2 hidden>{{state.uid=$route.params.id}}</h2>

  <div class="tablequestion">
    <table class="table table-hover">
      <thead>
        <tr>
          <th scope="col">질문 제목</th>
          <th scope="col">질문 내용</th>
        </tr>
      </thead>
      <tbody v-for="(list, i) in state.lists" :key="(list, i)">
        <tr v-if="list.questionPublic">
          <td>
            <RouterLink :to="{name: 'questionread', params:{id:list.questionid}}"
              style="text-decoration:none; font-weight:bold;">
              {{ list.title }}
            </RouterLink>
          </td>
          <td>{{ list.content }}</td>
        </tr>
      </tbody>
    </table>

  </div>



</template>


<script>
import { onMounted } from 'vue'
import { reactive, ref } from 'vue'
import axios from 'axios'

export default {
  name: "TheQuestion",

  setup() {

    onMounted(() => { loginHandler() })

    const value = ref(true)
    const state = reactive({
      lists: '',
      uid: '',
      email: sessionStorage.getItem('email'),
      token: sessionStorage.getItem('TOKEN'),
    });

    const ListToggle = () => {
      let table = document.querySelector('.tablequestion table');
      if (value.value === true) {
        table.style.display = '';
      }
      else {
        table.style.display = 'none';
      }
    };

    const loginHandler = async () => {
      const url = "http://localhost:9090/api/questionListForPublic"

      const headers = {
        //TODO send the right amound of tokent and receive the same...
        'Content-Type': 'application/json',
        Authorization: state.token,
        token: state.token,
      }
      const body = { uid: state.uid }
      await axios.post(url, body, { headers }).then(function (res) {
        if (res.status == 200) {
          state.lists = res.data.dto
        } else {
          alert('bad.')
        }
      })
    }
    return { value, state, ListToggle, loginHandler }
  }
};


</script>

<style scoped>
.answer_collapse {
  border: solid 1px grey;
  padding: 0 10px;
  margin: 10px 0;
}

.answer {
  display: flex;
}

.counter {
  display: flex;
  align-items: center;
  height: 80px;
  margin-right: 30px;
  background-color: lightgray;
  padding: 0 10px;
  border-radius: 30%;
}

.num {
  font-size: 30px;
  color: rgb(6, 90, 92);
  font-weight: bold;
  margin: 10px;
}

button {
  font-size: 20px;
  font-weight: bold;
}
</style>