<template>
  <h2 hidden>{{ state.uid = $route.params.id }}</h2>
  <div>
    <table class="table table-hover" style="table-layout: fixed">
      <thead>
        <tr>
          <th scope="col">그룹 이름</th>
          <th scope="col">그룹 설명</th>
          <th scope="col">역할</th>
        </tr>
      </thead>
      <tbody v-for="(list, i) in state.lists" :key="(list, i)">
        <tr v-if="list.privid">
          <td class="content">
            <RouterLink :to="{ name: 'circleread', params: { id: list.circleid } }"
              style="text-decoration:none;color:rgb(69,90,92);font-weight: 500;">{{ list.name }}</RouterLink>
          </td>
          <td class="content">{{ list.content }}</td>
          <td v-if="list.roleSet.includes('ROLE_FOUNDER')">그룹 리더</td>
          <td v-else>회원</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>
<script>
import { onMounted } from 'vue'
import { reactive, ref } from '@vue/reactivity'
import axios from 'axios';
export default {
  setup() {
    onMounted(() => { circleListbyUserHandler(); });
    const state = reactive({
      lists: "",
      check: "",
      privid: '',
      uid: '',
      email: sessionStorage.getItem("email"),
      token: sessionStorage.getItem("TOKEN"),
    });
    const circleListbyUserHandler = async () => {

      const url = "http://localhost:9090/api/circlelistForPublic";
      const headers = {
        "Content-Type": "application/json",
        "Authorization": state.token,
        "token": state.token,
      };
      const body = { uid: state.uid, email: state.email };
      await axios.post(url, body, { headers }).then(function (res) {
        if (res.data != null) {
          state.lists = res.data.dto;
          state.check = res.data.checkdto;
        }
        else {
          alert("bad.");
        }
      });
    };
    return { state, circleListbyUserHandler };
  },
}
</script>
  
<style scoped>
.content {
  width: 10px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  word-break: break-all;
}
</style>